import React, { Component } from 'react';
import Contact from './../../component/Contact/Contact';
class ContactList extends Component {

  render() {
    const { results, action, hired, searchValue } = this.props;
    return (
      <div className="container">
        <h1 className="slave-count">Padawanów: {results.length}</h1>
        <div className="userlist">
          {results.map(user => (
            <Contact user={user} action={action} hired={hired} key={user.login.username} searchValue={searchValue} />
          ))}
        </div>
      </div>
    );
  }
}

export default ContactList;



