import React, { Component } from 'react';
import ContactList from './ContactList/ContactList';
import './App.css';
import contact from '../contact.json';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      showHired: true,
      hired: [],
      searchValue: '',
      sort: 'ASC'
    }
  }

  toggleFavs = () => {
    this.setState({ showHired: !this.state.showHired })
  }

  hirePerson = (username) => {
    const nowHired = this.state.hired;
    if (!nowHired.some(hiredPerson => (hiredPerson === username))) {
      this.setState({
        hired: [...nowHired, username]
      })
    } else {
      const hired = nowHired.filter(hiredPerson => (hiredPerson !== username));
      this.setState({
        hired
      })
    }
  }

  changeSearch = (event) => {
    this.setState({
      searchValue: event.target.value
    })
  }

  clearSearch = (event) => {
    this.setState({
      searchValue: ''
    })
  }

  changeSort = () => {
    const { sort } = this.state;
    this.setState({
      sort: sort === 'ASC' ? 'DESC' : 'ASC'
    })
  }

  render() {

    const { results } = contact;

    const { searchValue, hired, sort, showHired } = this.state;

    const filtered = results.filter(user => (
      user.login.username.includes(searchValue)
    ))

    const persons = !showHired ? filtered.filter(user => (
      hired.some(hiredPerson => (hiredPerson === user.login.username))
    )) : filtered;

    const sorted = persons.sort((prev, next) => (
      sort === 'ASC' ?
        prev.login.username < next.login.username :
        prev.login.username > next.login.username
    ))

    const action = {
      hirePerson: this.hirePerson
    }

    return (
      <div>
        <div className="search-form">
          <span className="search-label">Szukaj mordeczki:</span>
          <input
            type="search"
            className="search-slave"
            value={searchValue}
            onChange={this.changeSearch.bind(this)}
          />
          <button className="search-clear"  onClick={this.clearSearch}>&nbsp;</button>
          <span className="search-label">Sortuj mordeczki:</span>
          <button className="sort-slave" onClick={this.changeSort}>Sort {sort === 'ASC' ? '↑' : '↓'}</button>
          <button onClick={this.toggleFavs} className="filter-slave">{this.state.showHired ? 'Show only favs' : 'Show all'}</button>
        </div>
        <ContactList results={sorted} action={action} hired={hired} searchValue={searchValue}/>
      </div>
    );
  }
}

export default App;



