import React, { Component } from 'react';

class Contact extends Component {

  hireme = () => {
    const { action, user } = this.props;
    action.hirePerson(user.login.username);
  }

  highlight = (inputText, keword) => {
    const index = inputText.indexOf(keword);
    return (
      <h4>
      <span>{inputText.substring(0, index)}</span>
      <mark>{inputText.substring(index, index + keword.length)}</mark>
      <span>{inputText.substring(index + keword.length)}</span>
      </h4>
    );
  }
  render() {
    const { user, hired, searchValue } = this.props;
    const isHired = hired.some(hiredPerson => (hiredPerson === user.login.username));
    return (
      <div className="user">
        <div className="user-image">
          <img src={user.picture.medium} alt={user.login.username} />
        </div>
        {isHired && <span className="fav-badge">&nbsp;</span>}
        {this.highlight(user.login.username, searchValue)}
        <h6>{user.name.first} {user.name.last}</h6>
        <button onClick={this.hireme} className="fancy-button">{!isHired ? 'Najmij najemnika' : 'odnajmij najemnika'}</button>
      </div>
    );
  }
}

export default Contact;



